import React, { Component } from "react";

class Counter extends Component {
  state = {
    value: this.props.value
  };

  render() {
    return (
      <div className="singleC">
        {this.props.children}
        <div className="clear" />
        <div className="flex">
          <span className={this.getClasses()}>{this.props.value}</span>
          <div className="buttons">
            <button
              onClick={() => this.props.addEvt(this.props.id)}
              className="spinning"
            >
              <i className="fi fi-plus-a fi-stack ellipse" />
            </button>

            <button
              onClick={() => this.props.deleteEvt(this.props.id)}
              className="delete"
            >
              <i className="fi fi-trash fi-stack ellipse" />
            </button>
          </div>
        </div>
      </div>
    );
  }

  handleIncrement = product => {
    this.setState({ value: this.state.value + 1 });
  };

  getClasses() {
    let classes = "numItems ";
    classes += this.state.value === 0 ? "warn" : "";
    return classes;
  }
}

export default Counter;
