import React, { Component } from "react";
import shop from "./../../assets/img/shop.svg";
import { TimelineLite } from "gsap/TweenMax";

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isHovered: false
    };
    this.handleHover = this.handleHover.bind(this);
    // reference to the DOM node
    this.myElements = [];
    // reference to the animation
    this.first = new TimelineLite({ paused: true });
    this.second = new TimelineLite({ paused: true });
  }

  handleHover() {
    this.setState(prevState => ({
      isHovered: !prevState.isHovered
    }));
  }
  componentDidMount() {
    this.first.to(this.myElements[0], 1, { x: -390 }).play();
    this.second.to(this.myElements[1], 1, { x: 450 }).play();
  }

  render() {
    const sectClass = this.state.isHovered ? "active about" : "about";

    return (
      <section className={sectClass}>
        <div className="inlineSkew imgSkew">
          <div className="cell">
            <div ref={div => (this.myElements[0] = div)}>
              <img
                src={shop}
                alt={"OnlineShop"}
                onMouseEnter={this.handleHover}
                onMouseLeave={this.handleHover}
              />
            </div>
          </div>
        </div>
        <div className="inlineSkew textSkew">
          <div className="cell">
            <div ref={div => (this.myElements[1] = div)}>
              <span>Shopping </span>
              <span>online</span>
              <div className="clear" />
              <span>has </span>
              <span>never </span>
              <span>been</span>
              <div className="clear" />
              <span>easier!</span>
            </div>
          </div>
        </div>

        <div className="clear" />
        <p>
          This is an example about my react experience: I worked on transiction,
          repeated items from a json and a simple summary list when you add a
          product. <br />
          <br />
          This are the tecnologies I used:
          <br />
          Node.js;
          <br />
          React;
          <br />
          Html5 and Sass;
          <br />
          Gulp to compile and minified Sass;
          <br />
          Greensock for animations;
          <br />
          Photoshop;
          <br />
        </p>
      </section>
    );
  }
}

export default About;
