import React, { Component } from "react";
import Counter from "../Sidebar/counter";
import ProductList from "../../data/test.json";
import { TimelineLite, CSSPlugin } from "gsap/all";

class Products extends Component {
  constructor(props) {
    super(props);
    this.state = { counters: [] };
    // cards, elements tha will be used in the tween
    this.cards = [];
    // the timeline instance
    this.tl = new TimelineLite({ paused: true });
  }

  componentDidMount() {
    this.tl.staggerTo(this.cards, 0.5, { autoAlpha: 1, y: -20 }, 0.1).play();
  }

  addCounter = row => {
    const { counters } = this.state;
    const existingRow = counters.filter(c => c.id === row.id)[0];

    if (existingRow !== undefined) {
      this.addItem(existingRow.id);
    } else {
      this.setState({ counters: counters.concat(row) });
    }
  };

  deleteRow = counterId => {
    const counters = this.state.counters.filter(c => c.id !== counterId);
    this.setState({ counters });
  };

  addItem = id => {
    const { counters } = this.state;
    const existingRow = counters.filter(c => c.id === id)[0];
    this.setState(Object.assign(existingRow, { value: existingRow.value + 1 }));
  };

  render() {
    return (
      <section className="product">
        <div id="grid">
          <div className="list">
            {ProductList.arrayOfProducts.map((singleProd, index) => (
              <div
                id={index}
                ref={div => (this.cards[index] = div)}
                onClick={() =>
                  this.addCounter({
                    id: singleProd.id,
                    name: singleProd.name,
                    price: singleProd.price,
                    value: 1
                  })
                }
                key={index}
                className="item"
              >
                <img src={singleProd.imgUrl} alt={singleProd.name} />
              </div>
            ))}
          </div>
        </div>
        <aside>
          <h2>Summary:</h2>

          {this.state.counters.length === 0
            ? "No products yet"
            : this.state.counters.map(sngProd => (
                <Counter
                  key={sngProd.id}
                  id={sngProd.id}
                  addEvt={this.addItem}
                  deleteEvt={this.deleteRow}
                  value={sngProd.value}
                >
                  <h4>
                    {sngProd.name} | {sngProd.price}$
                  </h4>
                </Counter>
              ))}
        </aside>
      </section>
    );
  }
}

export default Products;
